import React, { forwardRef, useEffect, useRef, useState } from 'react';
import { ArrowCounterclockwise, EnvelopeSlash, InputCursorText, Trash, WindowSidebar, XCircle } from 'react-bootstrap-icons';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { useQuery, useMutation } from '@tanstack/react-query';
import { Button } from 'primereact/button';
import { Column } from 'primereact/column';
import { DataTable } from 'primereact/datatable';
import { Dropdown } from 'primereact/dropdown';
import { ProgressSpinner } from 'primereact/progressspinner';
import { toast } from 'sonner';
import style from './enquiries.module.scss';
import { getListOfSubmissions, updateEnquirySubmission, deleteSubmission, restoreSubmission } from '../../../APIs/enquiries-api';
import { searchClients } from '../../../APIs/global-search-api';
import { getUserList } from '../../../APIs/task-api';
import { useAuth } from '../../../app/providers/auth-provider';
import { useTrialHeight } from '../../../app/providers/trial-height-provider';
import ViewEnquiryLead from '../../../features/business/enquiries/view-enquiry-lead/view-enquiry-lead';
import { FallbackImage } from '../../../shared/ui/image-with-fallback/image-avatar';
import Loader from '../../../shared/ui/loader/loader';
import NoDataFoundTemplate from '../../../ui/no-data-template/no-data-found-template';

const formatDate = (timestamp) => {
    if (!timestamp) return '-';
    // Handle Unix timestamp (in seconds)
    const date = new Date(+timestamp * 1000);
    const formatter = new Intl.DateTimeFormat("en-AU", {
        timeZone: 'Australia/Sydney',
        day: 'numeric',
        month: 'short',
        year: 'numeric'
    });
    return formatter.format(date);
};

const EnquiriesTable = forwardRef(({ searchValue, selectedSubmissions, setSelectedSubmissions, isShowDeleted, filterType, refetchTrigger, setRefetchTrigger, enquiriesCountQuery, statusFilter }, ref) => {
    const { trialHeight } = useTrialHeight();
    const { session } = useAuth();
    const navigate = useNavigate();
    const observerRef = useRef(null);
    const [submissions, setSubmissions] = useState([]);
    const [page, setPage] = useState(1);
    const [searchParams] = useSearchParams();
    const formId = searchParams.get('formId');
    const [sort, setSort] = useState({ sortField: 'id', sortOrder: -1 });
    const [tempSort, setTempSort] = useState({ sortField: 'id', sortOrder: -1 });
    const [hasMoreData, setHasMoreData] = useState(true);
    const [loading, setLoading] = useState(false);
    const [userGroups, setUserGroups] = useState([]);
    const [loadingSubmissionId, setLoadingSubmissionId] = useState(null);
    const [deleteSubmissionId, setDeleteSubmissionId] = useState(null);
    const [spammedSubmissionId, setSpammedSubmissionId] = useState(null);
    const [visible, setVisible] = useState(false);
    const [editData, setEditData] = useState(null);
    const [isMovingToSale, setIsMovingToSale] = useState(null);
    const [restoreSubmissionId, setRestoreSubmissionId] = useState(null);
    const [reactivatingSubmissionId, setReactivatingSubmissionId] = useState(null);
    const limit = 25;

    const deleteSubmissionMutation = useMutation({
        mutationFn: ({ submissionId }) => deleteSubmission(submissionId),
        onSuccess: () => {
            setRefetchTrigger && setRefetchTrigger((prev) => !prev);
            enquiriesCountQuery?.refetch();
            setDeleteSubmissionId(null);
            toast.success('Submission deleted successfully');
        },
        onError: (error) => {
            console.error('Error deleting submission:', error);
            setDeleteSubmissionId(null);
            toast.error('Failed to delete submission');
        }
    });

    const restoreSubmissionMutation = useMutation({
        mutationFn: ({ submissionId, status }) => restoreSubmission(submissionId, status),
        onSuccess: () => {
            setRefetchTrigger && setRefetchTrigger((prev) => !prev);
            enquiriesCountQuery?.refetch();
            setRestoreSubmissionId(null);
            toast.success('Submission restored successfully');
        },
        onError: (error) => {
            console.error('Error restoring submission:', error);
            setRestoreSubmissionId(null);
            toast.error('Failed to restore submission');
        }
    });

    const usersList = useQuery({ queryKey: ['getUserList'], queryFn: getUserList });

    useEffect(() => {
        const groups = [];

        // Desktop Users group
        if (usersList?.data?.users?.length > 0) {
            const activeDesktopUsers = usersList.data.users
                .filter((user) => user?.is_active)
                .map((user) => ({
                    id: user?.id,
                    first_name: user?.first_name,
                    last_name: user?.last_name,
                    photo: user?.photo || "",
                    has_photo: user?.has_photo
                }));

            if (activeDesktopUsers.length > 0) {
                groups.push({
                    label: 'Desktop User',
                    items: activeDesktopUsers
                });
            }
        }

        setUserGroups(groups);
    }, [usersList?.data]);

    useEffect(() => {
        setPage(1);
    }, [searchValue, isShowDeleted, filterType, refetchTrigger, formId, statusFilter]);

    useEffect(() => {
        const loadData = async () => {
            setLoading(true);
            let orgId = session?.organization?.id;
            if (!orgId) {
                toast.error('Organization ID is not available in session');
                setLoading(false);
                return;
            }

            let order = "";
            if (tempSort?.sortOrder === 1) order = `${tempSort.sortField}`;
            else if (tempSort?.sortOrder === -1) order = `-${tempSort.sortField}`;

            // Map filterType to API filter parameter
            let filterParam = '';
            if (filterType && filterType !== 'all') {
                filterParam = filterType;
            }

            // Build status filter parameter
            let statusFilterParam = '';
            if (statusFilter?.status?.length > 0) {
                statusFilterParam = statusFilter.status.map(s => s.value).join(',');
            }

            const data = await getListOfSubmissions(orgId, page, limit, searchValue, order, isShowDeleted, filterParam, formId, statusFilterParam);

            if (page === 1) setSubmissions(data.results);
            else {
                if (data?.results?.length > 0)
                    setSubmissions(prev => {
                        const existingIds = new Set(prev.map(sub => sub.id));
                        const newSubmissions = data.results.filter(sub => !existingIds.has(sub.id));
                        return [...prev, ...newSubmissions];
                    });
            }
            setSort(tempSort);
            setHasMoreData(data.count !== submissions.length);
            setLoading(false);
        };

        loadData();
    }, [page, searchValue, tempSort, isShowDeleted, filterType, session?.organization?.id, submissions.length, refetchTrigger, formId, statusFilter]);

    useEffect(() => {
        if (submissions.length > 0 && hasMoreData) {
            observerRef.current = new IntersectionObserver(entries => {
                if (entries[0].isIntersecting) setPage(prevPage => prevPage + 1);
            });

            const lastRow = document.querySelector('.p-datatable-tbody tr:not(.p-datatable-emptymessage):last-child');
            if (lastRow) observerRef.current.observe(lastRow);
        }

        return () => {
            if (observerRef.current) observerRef.current.disconnect();
        };
    }, [submissions, hasMoreData]);

    const formTitleBody = (rowData) => {
        return <div className={`d-flex align-items-center justify-content-between show-on-hover`}>
            <div className='d-flex align-items-center gap-2'>
                {
                    rowData.form_type == 'web' ? <div className={style.webIcon}>
                        <WindowSidebar size={12} color='#9E77ED' />
                    </div>
                        : <div className={style.formIcon}>
                            <InputCursorText size={12} color='#F79009' />
                        </div>
                }
                <div className='d-flex flex-column' style={{ lineHeight: '1.385' }}>
                    <span style={{ color: '#667085', fontSize: '14px' }}>{rowData.form_title || "-"}</span>
                    <span className='font-12' style={{ color: '#98A2B3' }}>{formatDate(rowData.submitted_at)}</span>
                </div>
            </div>
            <Button label="Open" onClick={() => { setVisible(true); setEditData({ id: rowData?.id, leadData: rowData }); }} className='primary-text-button ms-3 show-on-hover-element not-show-checked' text />
        </div>;
    };

    const nameBody = (rowData) => {
        return <div className='d-flex flex-column'>
            <span style={{ color: '#667085' }}>{rowData.data?.name || '-'}</span>
        </div>;
    };

    const emailBody = (rowData) => {
        return <div className='d-flex flex-column'>
            <span style={{ color: '#667085' }}>{rowData.data?.email || '-'}</span>
        </div>;
    };

    const phoneBody = (rowData) => {
        return <div className='d-flex flex-column'>
            <span style={{ color: '#667085' }}>{rowData.data?.phone || '-'}</span>
        </div>;
    };

    const assignedToBody = (rowData) => {
        const handleAssignChange = async (userId) => {
            try {
                await updateEnquirySubmission(rowData.id, { assigned_to: userId });
                setRefetchTrigger && setRefetchTrigger((prev) => !prev);
                enquiriesCountQuery?.refetch();
                toast.success('User assigned successfully');
            } catch (error) {
                console.error('Error assigning user:', error);
                toast.error('Failed to assign user');
            }
        };

        return (
            <div className='d-flex align-items-center'>
                <Dropdown
                    value={rowData.assigned_to || null}
                    onChange={(e) => handleAssignChange(e.value)}
                    options={userGroups}
                    optionLabel={(user) => `${user.first_name} ${user.last_name}`}
                    optionValue="id"
                    placeholder="Unassigned"
                    optionGroupLabel="label"
                    optionGroupChildren="items"
                    filter
                    filterInputAutoFocus={true}
                    dropdownIcon={<></>}
                    style={{
                        width: '100%',
                        border: 'none',
                        background: 'transparent',
                        boxShadow: 'none'
                    }}
                    panelStyle={{ minWidth: '200px' }}
                    scrollHeight="400px"
                    itemTemplate={(user) => (
                        <div className='d-flex align-items-center gap-2'>
                            <div className='d-flex justify-content-center align-items-center' style={{ width: '24px', height: '24px', borderRadius: '50%', overflow: 'hidden', border: '1px solid #dedede' }}>
                                <FallbackImage photo={user?.photo} has_photo={user?.has_photo} is_business={false} size={17} />
                            </div>
                            <span style={{ fontSize: '14px', color: '#667085' }}>
                                {user.first_name} {user.last_name}
                            </span>
                        </div>
                    )}
                    valueTemplate={(user) => {
                        if (!rowData.assigned_to) return <span style={{ color: '#98A2B3', fontSize: '14px' }}>-</span>;

                        // Find selected user from all groups
                        let selectedUser = null;
                        for (const group of userGroups) {
                            selectedUser = group.items.find(u => u?.id === user?.id);
                            if (selectedUser) break;
                        }

                        return selectedUser ? (
                            <div className='d-flex align-items-center gap-2'>
                                <div className='d-flex justify-content-center align-items-center' style={{ width: '24px', height: '24px', borderRadius: '50%', overflow: 'hidden', border: '1px solid #dedede' }}>
                                    <FallbackImage photo={selectedUser?.photo} has_photo={selectedUser?.has_photo} is_business={false} size={17} />
                                </div>
                                <span style={{ color: '#667085', fontSize: '14px' }}>
                                    {selectedUser.first_name} {selectedUser.last_name}
                                </span>
                            </div>
                        ) : <span style={{ color: '#98A2B3' }}>-</span>;
                    }}
                />
            </div>
        );
    };

    const spamActionBody = (rowData) => {
        // If already SPAM (4), show restore button
        if (rowData.status === 4) {
            const handleReactivate = async () => {
                setReactivatingSubmissionId(rowData.id);
                try {
                    // If assigned_to exists -> STATUS_ASSIGNED (1), else STATUS_NEW (0)
                    const newStatus = rowData.assigned_to ? 1 : 0;
                    await updateEnquirySubmission(rowData.id, { status: newStatus });
                    setRefetchTrigger && setRefetchTrigger((prev) => !prev);
                    toast.success('Enquiry restored successfully');
                } catch (error) {
                    console.error('Error restoring enquiry:', error);
                    toast.error('Failed to restore enquiry');
                } finally {
                    setReactivatingSubmissionId(null);
                }
            };

            return (
                <button
                    onClick={handleReactivate}
                    disabled={reactivatingSubmissionId === rowData.id}
                    style={{
                        padding: '6px',
                        borderRadius: '16px',
                        border: '1px solid #A9EFC5',
                        background: '#ECFDF3',
                        color: '#067647',
                        cursor: reactivatingSubmissionId === rowData.id ? 'not-allowed' : 'pointer',
                        transition: 'all 0.2s',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center'
                    }}
                    onMouseEnter={(e) => {
                        e.currentTarget.style.background = '#D1FAE5';
                    }}
                    onMouseLeave={(e) => {
                        e.currentTarget.style.background = '#ECFDF3';
                    }}
                    title="Restore from Spam"
                >
                    {reactivatingSubmissionId === rowData.id ? (
                        <ProgressSpinner style={{ width: '20px', height: '20px' }} strokeWidth="4" />
                    ) : (
                        <ArrowCounterclockwise size={20} color="#067647" />
                    )}
                </button>
            );
        }

        const handleSpam = async () => {
            try {
                setSpammedSubmissionId(rowData.id);
                await updateEnquirySubmission(rowData.id, { status: 4 });
                setRefetchTrigger && setRefetchTrigger((prev) => !prev);
                toast.success(`Marked as spam successfully`);
            } catch (error) {
                setSpammedSubmissionId(null);
                console.error('Error marking as spam:', error);
                toast.error('Failed to mark as spam');
            } finally {
                setSpammedSubmissionId(null);
            }
        };

        return (
            <button
                onClick={handleSpam}
                disabled={spammedSubmissionId === rowData.id}
                style={{
                    padding: '6px',
                    borderRadius: '16px',
                    border: '1px solid #EAECF0',
                    background: 'transparent',
                    color: '#667085',
                    cursor:  spammedSubmissionId === rowData.id ? 'not-allowed' : 'pointer',
                    transition: 'all 0.2s',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center'
                }}
                onMouseEnter={(e) => {
                    e.currentTarget.style.color = '#344054';
                    e.currentTarget.style.background = '#F9FAFB';
                }}
                onMouseLeave={(e) => {
                    e.currentTarget.style.color = '#667085';
                    e.currentTarget.style.background = 'transparent';
                }}
                title="Mark as spam"
            >
                {
                    spammedSubmissionId === rowData.id ? (
                        <ProgressSpinner style={{ width: '20px', height: '20px' }} strokeWidth="4" />
                    ) : <EnvelopeSlash size={20} color={spammedSubmissionId === rowData.id ? '#344054' : '#667085'} />
                }
            </button>
        );
    };

    const noGoActionBody = (rowData) => {
        // If already NO_GO (3), show restore button
        if (rowData.status === 3) {
            const handleReactivate = async () => {
                setReactivatingSubmissionId(rowData.id);
                try {
                    // If assigned_to exists -> STATUS_ASSIGNED (1), else STATUS_NEW (0)
                    const newStatus = rowData.assigned_to ? 1 : 0;
                    await updateEnquirySubmission(rowData.id, { status: newStatus });
                    setRefetchTrigger && setRefetchTrigger((prev) => !prev);
                    toast.success('Enquiry restored successfully');
                } catch (error) {
                    console.error('Error restoring enquiry:', error);
                    toast.error('Failed to restore enquiry');
                } finally {
                    setReactivatingSubmissionId(null);
                }
            };

            return (
                <button
                    onClick={handleReactivate}
                    disabled={reactivatingSubmissionId === rowData.id}
                    style={{
                        padding: '0',
                        borderRadius: '8px',
                        border: '1px solid #A9EFC5',
                        background: '#ECFDF3',
                        color: '#067647',
                        cursor: reactivatingSubmissionId === rowData.id ? 'not-allowed' : 'pointer',
                        transition: 'all 0.2s',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        width: '42px',
                        height: '42px',
                        opacity: reactivatingSubmissionId === rowData.id ? 0.6 : 1
                    }}
                    onMouseEnter={(e) => {
                        e.currentTarget.style.background = '#D1FAE5';
                    }}
                    onMouseLeave={(e) => {
                        e.currentTarget.style.background = '#ECFDF3';
                    }}
                    title="Restore from No Go"
                >
                    {reactivatingSubmissionId === rowData.id ? (
                        <ProgressSpinner style={{ width: '20px', height: '20px' }} strokeWidth="4" />
                    ) : (
                        <ArrowCounterclockwise size={20} color="#067647" />
                    )}
                </button>
            );
        }

        const handleNoGo = async () => {
            setLoadingSubmissionId(rowData.id);
            try {
                await updateEnquirySubmission(rowData.id, { status: 3 });
                setRefetchTrigger && setRefetchTrigger((prev) => !prev);
                toast.success('Marked as No Go successfully');
            } catch (error) {
                console.error('Error marking as No Go:', error);
                toast.error('Failed to mark as No Go');
            } finally {
                setLoadingSubmissionId(null);
            }
        };

        return (
            <button
                onClick={handleNoGo}
                disabled={loadingSubmissionId === rowData.id}
                style={{
                    padding: '0',
                    borderRadius: '8px',
                    border: '1px solid #EAECF0',
                    background: '#FFFFFF',
                    color: '#F04438',
                    cursor: loadingSubmissionId === rowData.id ? 'not-allowed' : 'pointer',
                    transition: 'all 0.2s',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    width: '42px',
                    height: '42px',
                    opacity: loadingSubmissionId === rowData.id ? 0.6 : 1
                }}
                onMouseEnter={(e) => {
                    if (loadingSubmissionId !== rowData.id) {
                        e.currentTarget.style.background = '#FEF3F2';
                    }
                }}
                onMouseLeave={(e) => {
                    if (loadingSubmissionId !== rowData.id) {
                        e.currentTarget.style.background = '#FFFFFF';
                    }
                }}
                title="Mark as No Go"
            >
                {loadingSubmissionId === rowData.id ? (
                    <ProgressSpinner style={{ width: '20px', height: '20px' }} strokeWidth="4" />
                ) : (
                    <XCircle size={20} color="#D92D20" />
                )}
            </button>
        );
    };

    const actionBody = (rowData) => {
        const handleDelete = async () => {
            setDeleteSubmissionId(rowData.id);
            deleteSubmissionMutation.mutate({
                submissionId: rowData.id
            });
        };

        const handleRestore = async () => {
            setRestoreSubmissionId(rowData.id);
            // Determine status based on assignment:
            // If user is assigned (assigned_to exists), set to STATUS_ASSIGNED (1)
            // If no user assigned, set to STATUS_NEW (0)
            const restoreStatus = rowData.assigned_to ? 1 : 0;
            restoreSubmissionMutation.mutate({
                submissionId: rowData.id,
                status: restoreStatus
            });
        };

        // If showing deleted items, show restore button instead of delete
        if (isShowDeleted) {
            return (
                <button
                    onClick={handleRestore}
                    disabled={restoreSubmissionId === rowData.id}
                    style={{
                        padding: '0',
                        borderRadius: '8px',
                        border: '1px solid #A9EFC5',
                        background: '#ECFDF3',
                        color: '#067647',
                        cursor: restoreSubmissionId === rowData.id ? 'not-allowed' : 'pointer',
                        transition: 'all 0.2s',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        width: '42px',
                        height: '42px',
                        opacity: restoreSubmissionId === rowData.id ? 0.6 : 1
                    }}
                    onMouseEnter={(e) => {
                        if (restoreSubmissionId !== rowData.id) {
                            e.currentTarget.style.background = '#D1FAE5';
                        }
                    }}
                    onMouseLeave={(e) => {
                        if (restoreSubmissionId !== rowData.id) {
                            e.currentTarget.style.background = '#ECFDF3';
                        }
                    }}
                    title="Restore Submission"
                >
                    {restoreSubmissionId === rowData.id ? (
                        <ProgressSpinner style={{ width: '20px', height: '20px' }} strokeWidth="4" />
                    ) : (
                        <ArrowCounterclockwise size={20} color="#067647" />
                    )}
                </button>
            );
        }

        return (
            <button
                onClick={handleDelete}
                disabled={deleteSubmissionId === rowData.id}
                style={{
                    padding: '0',
                    borderRadius: '8px',
                    border: '1px solid #EAECF0',
                    background: '#FFFFFF',
                    color: '#F04438',
                    cursor: deleteSubmissionId === rowData.id ? 'not-allowed' : 'pointer',
                    transition: 'all 0.2s',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    width: '42px',
                    height: '42px',
                    opacity: deleteSubmissionId === rowData.id ? 0.6 : 1
                }}
                onMouseEnter={(e) => {
                    if (deleteSubmissionId !== rowData.id) {
                        e.currentTarget.style.background = '#FEF3F2';
                    }
                }}
                onMouseLeave={(e) => {
                    if (deleteSubmissionId !== rowData.id) {
                        e.currentTarget.style.background = '#FFFFFF';
                    }
                }}
                title="Delete Submission"
            >
                {deleteSubmissionId === rowData.id ? (
                    <ProgressSpinner style={{ width: '20px', height: '20px' }} strokeWidth="4" />
                ) : (
                    <Trash size={20} color="#D92D20" />
                )}
            </button>
        );
    };

    const moveToQuoteActionBody = (leadData) => {
        const handleMoveToQuote = async () => {
            const name = leadData?.data?.name;
            const email = leadData?.data?.email;
            if (!name) {
                const enquiryData = {
                    name: leadData?.data?.name || '',
                    email: leadData?.data?.email || '',
                    phone: leadData?.data?.phone || '',
                    formData: leadData?.data || {},
                    enquiryId: leadData?.id || null,
                    formTitle: leadData?.form_title || ''
                };
                sessionStorage.setItem('enquiry-to-sale', JSON.stringify(enquiryData));
                navigate('/sales/newquote/selectyourclient/new-clients');
                return;
            }

            setIsMovingToSale(leadData.id);

            try {
                // Search for client by email
                const response = await searchClients(name, 100);
                const clients = response?.results || [];

                // Find exact email match
                const existingClient = clients.find(client => {
                    // Check client email
                    if (client.email?.toLowerCase() === email.toLowerCase()) {
                        return true;
                    }

                    // check client name
                    if (client.name?.toLowerCase() === name.toLowerCase()) {
                        return true;
                    }

                    return false;
                });

                if (existingClient) {
                    navigate(`/sales/newquote/selectyourclient/client-information/scope-of-work/${existingClient.id}`);
                } else {
                    // Client doesn't exist, store lead data and navigate to new client page
                    const enquiryData = {
                        name: leadData?.data?.name || '',
                        email: email,
                        phone: leadData?.data?.phone || '',
                        formData: leadData?.data || {},
                        enquiryId: leadData?.id || null,
                        formTitle: leadData?.form_title || ''
                    };
                    sessionStorage.setItem('enquiry-to-sale', JSON.stringify(enquiryData));
                    navigate('/sales/newquote/selectyourclient/new-clients');
                }
            } catch (error) {
                console.error('Error searching for client:', error);
                toast.error('Failed to search for client. Please try again.');
            } finally {
                setIsMovingToSale(null);
            }
        };

        return (
            <button
                onClick={handleMoveToQuote}
                style={{
                    padding: '8px 18px',
                    borderRadius: '24px',
                    border: '1px solid #A9EFC5',
                    background: '#ECFDF3',
                    color: '#067647',
                    cursor: leadData?.status === 2 || isMovingToSale === leadData.id ? 'not-allowed' : 'pointer',
                    fontSize: '14px',
                    fontWeight: 600,
                    transition: 'all 0.2s',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    gap: '4px'
                }}
                onMouseEnter={(e) => {
                    e.currentTarget.style.background = '#D1FAE5';
                    e.currentTarget.style.borderColor = '#A7F3D0';
                }}
                onMouseLeave={(e) => {
                    e.currentTarget.style.background = '#ECFDF5';
                    e.currentTarget.style.borderColor = '#D1FAE5';
                }}
                title="Move to Sale"
                disabled={isMovingToSale === leadData.id || leadData?.status === 2}
            >
                Move
                {
                    isMovingToSale === leadData.id ? (
                        <ProgressSpinner style={{ width: '16px', height: '16px' }} strokeWidth="4" />
                    ) : <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 20 20" fill="none">
                        <path fillRule="evenodd" clipRule="evenodd" d="M4.55806 2.05806C4.80214 1.81398 5.19786 1.81398 5.44194 2.05806L12.9419 9.55806C13.186 9.80214 13.186 10.1979 12.9419 10.4419L5.44194 17.9419C5.19786 18.186 4.80214 18.186 4.55806 17.9419C4.31398 17.6979 4.31398 17.3021 4.55806 17.0581L11.6161 10L4.55806 2.94194C4.31398 2.69786 4.31398 2.30214 4.55806 2.05806Z" fill="#079455" />
                        <path fillRule="evenodd" clipRule="evenodd" d="M9.55806 2.05806C9.80214 1.81398 10.1979 1.81398 10.4419 2.05806L17.9419 9.55806C18.186 9.80214 18.186 10.1979 17.9419 10.4419L10.4419 17.9419C10.1979 18.186 9.80214 18.186 9.55806 17.9419C9.31398 17.6979 9.31398 17.3021 9.55806 17.0581L16.6161 10L9.55806 2.94194C9.31398 2.69786 9.31398 2.30214 9.55806 2.05806Z" fill="#079455" />
                    </svg>
                }

            </button>
        );
    };

    const onSort = (event) => {
        const { sortField, sortOrder } = event;
        setTempSort({ sortField, sortOrder });
        setPage(1);
    };

    return (
        <>
            <DataTable
                ref={ref}
                value={submissions}
                scrollable
                selectionMode={'checkbox'}
                columnResizeMode="expand"
                resizableColumns
                showGridlines
                size={'large'}
                scrollHeight={`calc(100vh - 175px - ${trialHeight}px)`}
                className="border"
                selection={selectedSubmissions}
                onSelectionChange={(e) => setSelectedSubmissions(e.value)}
                loading={loading}
                loadingIcon={Loader}
                emptyMessage={<NoDataFoundTemplate isDataExist={!!searchValue} />}
                sortField={sort?.sortField}
                sortOrder={sort?.sortOrder}
                onSort={onSort}
            >
                <Column
                    selectionMode="multiple"
                    headerClassName='ps-4 border-end-0'
                    bodyClassName={'show-on-hover border-end-0'}
                    headerStyle={{ width: '1rem', textAlign: 'center' }}
                    frozen
                />
                <Column
                    field="form_title"
                    header="Lead Source"
                    body={formTitleBody}
                    style={{ minWidth: '160px' }}
                    sortable
                    headerClassName='shadowRight ps-0'
                    bodyClassName='shadowRight ps-0'
                    frozen
                />
                <Column
                    field="data.name"
                    header="Name"
                    body={nameBody}
                    style={{ minWidth: '160px' }}
                />
                <Column
                    field="data.phone"
                    header="Phone"
                    body={phoneBody}
                    style={{ minWidth: '140px' }}
                />
                <Column
                    field="data.email"
                    header="Email"
                    body={emailBody}
                    style={{ minWidth: '220px' }}
                />
                <Column
                    header={
                        <span style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
                            Assign To
                            <span style={{ fontSize: '11px', color: '#98A2B3', fontWeight: 400 }}>AZ</span>
                            <svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" viewBox="0 0 24 24" fill="none" stroke="#98A2B3" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                                <polyline points="18 15 12 9 6 15"></polyline>
                            </svg>
                        </span>
                    }
                    body={assignedToBody}
                    style={{ minWidth: '180px' }}
                    sortable
                    field="assigned_to.name"
                />
                <Column
                    header="Spam"
                    body={spamActionBody}
                    style={{ minWidth: '90px', maxWidth: '90px', width: '90px', textAlign: 'center' }}
                    hidden={isShowDeleted}
                />
                <Column
                    header="No Go"
                    body={noGoActionBody}
                    style={{ minWidth: '90px', maxWidth: '90px', width: '90px', textAlign: 'center' }}
                    hidden={isShowDeleted}
                />
                <Column
                    header="Move to Sale"
                    body={moveToQuoteActionBody}
                    style={{ minWidth: '130px', maxWidth: '150px', width: '150px' }}
                    hidden={isShowDeleted}
                />
                <Column
                    header={isShowDeleted ? "Restore" : "Actions"}
                    body={actionBody}
                    style={{ minWidth: '90px', maxWidth: '90px', width: '90px', textAlign: 'center' }}
                />
            </DataTable>
            {visible && editData?.id && (
                <ViewEnquiryLead
                    visible={visible}
                    editData={editData}
                    usersList={usersList?.data?.users || []}
                    onClose={() => {
                        setVisible(false);
                        setEditData(null);
                    }}
                />
            )}
        </>
    );
});

export default EnquiriesTable;